import { NgModule } from '@angular/core';
import { APIBaseUrlProvider, ApplicationIdentifier, ConfigService, CORE_PROVIDERS, ENVIRONMENT } from '@tei/core';
import { LoadingModule } from '@tei/material';
import { NbMenuModule } from '@tei/nebular-theme';
import { ExtendedAuthenticationModule, ExtendedAuthenticationService } from '@tei/pos-shared';
import { environment } from '../../../projects/app-core-lib/src/lib/@core/environments/environment';
import { ThemeModule } from '../../../projects/app-core-lib/src/lib/@theme/theme.module';
import { DashboardModule } from '../../../projects/app-core-lib/src/lib/pages/dashboard/dashboard.module';
import { DefaultRouteHandlerComponent } from '../../../projects/app-core-lib/src/lib/pages/default-route-handler/default-route-handler.component';
import { MiscellaneousModule } from '../../../projects/app-core-lib/src/lib/pages/miscellaneous/miscellaneous.module';
import { PagesComponent } from '../../../projects/app-core-lib/src/lib/pages/pages.component';
import { PortalGroupsModule } from '../../../projects/app-core-lib/src/lib/pages/portal-groups/portal-groups.module';
import { PortalsModule } from '../../../projects/app-core-lib/src/lib/pages/portals/portals.module';
import { WebModule } from '../../../projects/app-core-lib/src/lib/pages/web/web.module';
import { PagesRoutingModule } from './pages-routing.module';


@NgModule({
  imports: [
    PagesRoutingModule,
    ThemeModule,
    NbMenuModule,
    DashboardModule,
    PortalGroupsModule,
    PortalsModule,
    MiscellaneousModule,
    WebModule,
    LoadingModule,
    ExtendedAuthenticationModule.forRoot({
      modules: [
        {
          moduleKey: ApplicationIdentifier.Retail,
          moduleStrategy: {
            login: {
              endpoint: '/login',
              redirect: {
                success: '/sell'
              },
            },
            logout: {
              endpoint: '/logout',
              redirect: {
                success: '/login'
              },
            }
          }
        },
        {
          moduleKey: ApplicationIdentifier.FoodAndBeverage,
          moduleStrategy: {
            login: {
              endpoint: '/login',
              redirect: {
                success: '/orders'
              },
            },
            logout: {
              endpoint: '/logout',
              redirect: {
                success: '/login'
              },
            }
          }
        },
      ]
    })
  ],
  declarations: [
    PagesComponent,
    DefaultRouteHandlerComponent,
  ],
  providers: [
    ...CORE_PROVIDERS,
    { provide: ENVIRONMENT, useValue: environment },
    { provide: 'IConfigService', useClass: ConfigService },
    { provide: 'IAPIBaseUrlProvider', useClass: APIBaseUrlProvider },
    { provide: 'IExtendedAuthenticationService', useClass: ExtendedAuthenticationService },
  ]
})

export class PagesModule
{
}
